#ct{
    min-height:30vh; 
    width:90%;
    margin: 0 auto;
    text-align:center;
    position:relative;
    color:#fff;
    padding:15px 0; 
    border-radius:5px;
    display:flex;
    flex-direction: column;
    
}

#ct::before, #ct:after {
  width: 100%;
  height:100%;
  z-index: 1;
  content:'';
  position: absolute;
  top:0;
  left:0;
  transform: scale(0);
  -webkit-transform: scale(0);
  -webkit-transition: 1s all ease-in-out;
  transition: 1s all ease-in-out;
  -webkit-transition-delay: 0.3s;
          transition-delay: 0.3s;
}

#ct.active::after, #ct.active::before {
  -webkit-transform: scale(1);
  transform: scale(1);
}

#ct.active::before {
  border-bottom: 2px solid #7e57c2;
  border-left: 2px solid #7e57c2;
  transform-origin: 0 100%;
  -webkit-transform-origin: 0 100%;
}

#ct.active::after {
  border-top: 2px solid #7e57c2;
  border-right: 2px solid #7e57c2;
  transform-origin: 100% 0;
  -webkit-transform-origin: 100% 0%;
}


#ct span{
  background:#141414;
  color:#7e57c2;
  padding:0 15px;
  font-size:6vh;
  position:relative; 
  top:-1em;
  left:0px;
  z-index:3;
  display:inline-block;
}

#ct p{font-size:2.5vh}

#ct blockquote {
    -webkit-transition: all 900ms cubic-bezier(0.165, 0.84, 0.44, 1);
    transition: all 900ms cubic-bezier(0.165, 0.84, 0.44, 1);
    -webkit-transition-delay: 0.3s;
            transition-delay: 0.3s;
    margin-top: 120px;
    opacity: 0;
  }
  
 #ct blockquote.active {
    -webkit-transform: translateY(-90px);
    transform: translateY(-90px);
    opacity: 1;
  }
